ia-label=\"Next\" @click=\"nextPage()\" class=\"page-link\">\n          <slot name=\"nextIcon\" :disabled=\"currentPage === totalPages\">\n            <svg width=\"16\" height=\"16\" role=\"img\" xmlns=\"http://www.w3.org/2000/svg\" viewBox=\"0 0 320 512\">\n              <path fill=\"currentColor\"\n                    d=\"M285.476 272.971L91.132 467.314c-9.373 9.373-24.569 9.373-33.941 0l-22.667-22.667c-9.357-9.357-9.375-24.522-.04-33.901L188.505 256 34.484 101.255c-9.335-9.379-9.317-24.544.04-33.901l22.667-22.667c9.373-9.373 24.569-9.373 33.941 0L285.475 239.03c9.373 9.372 9.373 24.568.001 33.941z\"></path>\n            </svg>\n          </slot>\n        </a>\n      </li>\n\n      <li :class=\"{'disabled': currentPage === totalPages}\" v-if=\"boundaryLinks\"\n          class=\"page-item\">\n        <a href=\"javascript:void(0)\" aria-label=\"Previous\" @click=\"lastPage()\" class=\"page-link\">\n          <span aria-hidden=\"true\" v-html=\"lastText\"></span>\n        </a>\n      </li>\n    </ul>\n  </nav>\n</template>\n\n<script>\nexport default {\n  name: 'SmartPagination',\n  props: {\n    currentPage: {\n      required: true,\n      type: Number\n    },\n    totalPages: {\n      required: true,\n      type: Number\n    },\n    hideSinglePage: {\n      required: false,\n      type: Boolean,\n      default: true\n    },\n    maxPageLinks: {\n      required: false,\n      type: Number\n    },\n    boundaryLinks: {\n      required: false,\n      type: Boolean,\n      default: false\n    },\n    firstText: {\n      required: false,\n      type: String,\n      default: 'First'\n    },\n    lastText: {\n      required: false,\n      type: String,\n      default: 'Last'\n    },\n    directionLinks: {\n      required: false,\n      type: Boolean,\n      default: true\n    }\n  },\n  computed: {\n    displayPages () {\n      if (isNaN(this.maxPageLinks) || this.maxPageLinks <= 0) {\n        return this.displayAllPages()\n      } else {\n        return this.limitVisiblePages()\n      }\n    }\n  },\n  methods: {\n    displayAllPages () {\n      const displayPages = []\n\n      for (let i = 1; i <= this.totalPages; i++) {\n        displayPages.push({\n          title: i.toString(),\n          value: i\n        })\n      }\n      return displayPages\n    },\n    limitVisiblePages () {\n      const displayPages = []\n\n      const totalTiers = Math.ceil(this.totalPages / this.maxPageLinks)\n\n      const activeTier = Math.ceil(this.currentPage / this.maxPageLinks)\n\n      const start = ((activeTier - 1) * this.maxPageLinks) + 1\n      const end = start + this.maxPageLinks\n\n      if (activeTier > 1) {\n        displayPages.push({\n          title: '...',\n          value: start - 1\n        })\n      }\n\n      for (let i = start; i < end; i++) {\n        if (i > this.totalPages) {\n          break\n        }\n\n        displayPages.push({\n          title: i.toString(),\n          value: i\n        })\n      }\n\n      if (activeTier < totalTiers) {\n        displayPages.push({\n          title: '...',\n          value: end\n        })\n      }\n\n      return displayPages\n    },\n    selectPage (page) {\n      if (page < 1 || page > this.totalPages || page === this.currentPage) {\n        return\n      }\n\n      this.$emit('update:currentPage', page)\n    },\n    nextPage () {\n      if (this.currentPage < this.totalPages) {\n        this.$emit('update:currentPage', this.currentPage + 1)\n      }\n    },\n    previousPage () {\n      if (this.currentPage > 1) {\n        this.$emit('update:currentPage', this.currentPage - 1)\n      }\n    },\n    firstPage () {\n      this.$emit('update:currentPage', 1)\n    },\n    lastPage () {\n      this.$emit('update:currentPage', this.totalPages)\n    }\n  }\n}\n</script>\n\n<style>\n  .disabled svg {\n    color: grey;\n  }\n\n  .disabled a {\n    cursor: not-allowed\n  }\n</style>\n"],"sourceRoot":""}]);
